// @flow
import * as React from 'react';
import { STEPS_AWAITING_SELLER_ACTION } from '../../../shared/Consts';
import { instructionContent } from './helpers';
import _get from 'lodash/get';
import { Box, Typography } from '@material-ui/core';

type Props = {
  stepId: number,
};

const OfferInstruction = ({
  stepId,
}: Props): React.Element<typeof Box | typeof React.Fragment> => {
  const isSellerReviewOffer = STEPS_AWAITING_SELLER_ACTION.includes(stepId);

  if (!isSellerReviewOffer) return <></>;

  return (
    <Box sx={{ p: 2 }}>
      <Typography variant="h5">
        {_get(instructionContent, `${stepId}.instruction`)}
      </Typography>
    </Box>
  );
};

export default (React.memo(OfferInstruction): React.AbstractComponent<Props>);
