// @flow
import { Grid, makeStyles, Typography } from '@material-ui/core';
import React, { Fragment, useEffect, useState } from 'react';
import type { Element, AbstractComponent } from 'react';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import { saveOfferStep } from '../redux/Actions';
import { OFFER_STEPS, ACCEPT_STEPS } from 'shared/Consts';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { toastr } from 'react-redux-toastr';
import { OFFER_ERROR_MESSAGES } from 'shared/Messages';
import { PageWrapper } from '../../../components/PageWrapper';
import withCustomAuthenticator from 'containers/Authenticator/withCustomAuthenticator';
import { Widget } from 'components/Widget';
import { mappingCounterData } from '../../../helpers/Offer';
import BlockUi from 'react-block-ui';
import { Loader } from 'react-loaders';
import CounterOfferModal from 'components/Offers/CounterOfferModal';
import { OffersTable } from './OffersTable';
import { ConfirmDialog } from 'components/ConfirmDialog';
import { ViewOfferDialog } from './ViewOfferDialog';
import clsx from 'clsx';
import { useQuery } from '@apollo/client';
import { GET_SELLER_OFFERS } from 'graphql/queries/user/overview';
import { getSellerOffers, instructionContent } from './helpers';

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '56px',
  },
  widget: {
    padding: 0,
  },
  notFound: {
    padding: '1rem',
  },
}));

type PropsWithoutRedux = {};

type Props = {
  isLoading: boolean,
  saveOfferStep: Function,
  isUpdatedStep: boolean,
  error: Object,
  isFormLoaded: boolean,
};

const OfferList = (props: Props): Element<typeof Fragment> => {
  const { isLoading, saveOfferStep, isUpdatedStep, error, isFormLoaded } =
    props;

  const {
    loading: fetchLoading,
    data,
    errors: fetchError,
    refetch,
  } = useQuery(GET_SELLER_OFFERS, { errorPolicy: 'all' });

  const offers = getSellerOffers(data);

  const classes = useStyles();
  const [openOfferDialog, setOpenOfferDialog] = useState(false);
  const [openCounterOfferDialog, setOpenCounterOfferDialog] = useState(false);
  const [openConfirmDialog, setOpenConfirmDialog] = useState(false);
  const [confirmDialogProps, setConfirmDialogProps] = useState({
    children: '',
    onAgree: () => '',
  });
  const [selectedOffer, setSelectedOffer] = useState(undefined);

  useEffect(() => {
    if (isUpdatedStep) {
      refetch();
      handleClickCloseDialog();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isUpdatedStep]);

  useEffect(() => {
    const { message } = error || {};
    const { message: statusesMessage } = fetchError || {};
    if (message || statusesMessage) {
      toastr.error(OFFER_ERROR_MESSAGES.saveFailure, message);
    }
  }, [error, fetchError]);

  const handleViewOffer = offer => () => {
    setSelectedOffer(offer);
    setOpenOfferDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenOfferDialog(false);
  };

  const handleOpenAcceptConfirm = (offerId, stepId) => {
    if (stepId === OFFER_STEPS.sellerNeedToCheckCounter) {
      return handleAccept(offerId, stepId);
    }
    setOpenConfirmDialog(true);
    setConfirmDialogProps({
      children: _get(instructionContent, `${stepId}.confirmation`, ''),
      onAgree: () => {
        setOpenConfirmDialog(
          stepId === OFFER_STEPS.sellerNeedToReReviewSuggestion,
        );
        handleAccept(offerId, stepId);
      },
    });
  };

  const handleAccept = (offerId, stepId) => {
    const newStepId = _get(ACCEPT_STEPS, stepId, OFFER_STEPS.paBeingCreated);
    saveOfferStep(offerId, { new_step: newStepId });
  };

  const handleDecline = offerId => {
    saveOfferStep(offerId, {
      new_step: OFFER_STEPS.advisorNeedToReReviewSuggestion,
    });
  };

  const handleClickOpenCounterDialog = offer => {
    setSelectedOffer(offer);
    setOpenOfferDialog(false);
    setOpenCounterOfferDialog(true);
  };

  const handleClickCloseDialog = ({ isReload } = {}) => {
    setSelectedOffer();
    setOpenCounterOfferDialog(false);
    setOpenOfferDialog(false);
    setOpenConfirmDialog(false);

    if (isReload) {
      refetch();
    }
  };

  const mappingOffers = offers ? offers.map(mappingCounterData) : [];
  const mappingSelectedOffer =
    selectedOffer && mappingCounterData(selectedOffer);

  return (
    <PageWrapper>
      <Grid container justifyContent="center">
        <Grid item xs={12}>
          <Widget className={classes.widget}>
            <BlockUi
              tag="div"
              blocking={fetchLoading}
              loader={
                <Loader active type="ball-pulse" color="rgba(231,28,99,1)" />
              }
              className={clsx(fetchLoading && classes.container)}
            >
              {!_isEmpty(offers) && (
                <OffersTable
                  offers={mappingOffers}
                  onViewOffer={handleViewOffer}
                />
              )}
              {_isEmpty(offers) && !fetchLoading && (
                <Grid container className={classes.notFound}>
                  <Typography variant="h3">No offer found</Typography>
                </Grid>
              )}
            </BlockUi>
          </Widget>
        </Grid>
      </Grid>
      <ViewOfferDialog
        open={openOfferDialog}
        offer={mappingSelectedOffer}
        onClose={handleCloseDialog}
        handleApproval={handleOpenAcceptConfirm}
        handleDecline={handleDecline}
        handleCounter={handleClickOpenCounterDialog}
        isSubmitting={isLoading}
      />
      <CounterOfferModal
        offer={mappingSelectedOffer}
        open={openCounterOfferDialog}
        handleClickCloseDialog={handleClickCloseDialog}
        isFormLoaded={isFormLoaded}
      />
      <ConfirmDialog
        open={openConfirmDialog}
        onClose={() => setOpenConfirmDialog(false)}
        {...confirmDialogProps}
      />
    </PageWrapper>
  );
};

const structuredSelector = createStructuredSelector({
  isLoading: state => _get(state, ['sellerOffers', 'isLoading']),
  isUpdatedStep: state => _get(state, ['sellerOffer', 'isUpdatedStep']),
  error: state => _get(state, ['sellerOffer', 'error']),
  isFormLoaded: state => _get(state, ['form', 'isFormLoaded']),
});

const mapDispatchToProps = {
  saveOfferStep,
};

export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(
  withErrorBoundary(withCustomAuthenticator(OfferList)),
): AbstractComponent<PropsWithoutRedux>);
